
@font-face {
  font-family: 'VigranMaroll';
  src: url('./VigranMaroll.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}


.name-div{
  margin: 0;
  padding: 0;
  height: 100vh; /* Full viewport height */
  display: flex;
  justify-content: center;
  align-items: center
}

.name-container {
  width: 700px;
  height: 400px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.name-line {
  position: absolute;
  right: 0;
  left: 0;
  top: -40px;
  background-color: white;
  width: 1px;
  height: 150px; /* Adjusted from 0px */
  background-image: linear-gradient(to bottom, #F0C27B, #4B1248);
  background-size: 200% 200%;
  animation: AnimationName 5s ease infinite;
}

.name-container .name-line-1 {
  /* border-left: 5px solid black; */
  backface-visibility: hidden; /* Added standard property */
  -webkit-backface-visibility: hidden;
  left: 341px;
  top: 10px;
}

.name-container .name-line-2 {
  /* border-right: 5px solid black; */
  left: 341px;
  top: 10px;

}

.name-container .name-text {
  color: white;
  text-transform: uppercase;
  font-family: 'VigranMaroll';
  font-weight: 300;
  font-size: 75px;
  text-align: left;
  width: 230px; /* Adjusted from 0px */
  overflow: hidden;
  transform: rotate(35deg);
  position: relative;
  height: 100%;
  visibility: hidden;
  transform-origin: center; /* Set the origin of scaling */
  animation: scaleText 2.5s ease-out forwards;
}

.name-container .name-text .name-span {
  transform: rotate(-35deg);
  position: absolute;
  width: 290px;
  top: 48px;
  right: 0;
  left: -7px;
  background-image: linear-gradient(to bottom, #F0C27B, #4B1248);
  background-clip: text; /* Added standard property */
  -webkit-background-clip: text;
  text-fill-color: transparent; /* Added standard property */
  -webkit-text-fill-color: transparent;
  color: #fff;
  background-size: 200% 200%;
  animation: AnimationName 5s ease infinite;
}

@keyframes scaleText {
  0% {
    transform: scale(0) 35deg;
  }
  100% {
    transform: scale(1) 35deg;
  }
}

@keyframes AnimationName {
  0% { background-position: 0% 88%; }
  50% { background-position: 100% 13%; }
  100% { background-position: 0% 88%; }
}


.about-me{
  color: white;
  padding: 20px;
  width: 50%;
}

.about-me-header {
  font-size: 40px;
  font-family: 'Robot9000';
}

.about-me-desc {
  padding-right: 130px;
  font-size: 25px;
  margin-top: 25px;
  font-family: '8bitOperator';
}

.about-me-div {
  height: 90vh;
  background: linear-gradient(180deg, #000 0%, #4658A4 78.45%);
  display: flex;
  flex-direction: row;
  align-items: center;
}

.all-social-div {
  margin-top: 40px;
  display: flex;
  flex-direction: row;
  gap: 20px;
}

.social-div {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 110px;
}


.btn-class-name {
  --primary: 255, 90, 120;
  --secondary: 150, 50, 60;
  width: 60px;
  height: 50px;
  border: none;
  outline: none;
  cursor: pointer;
  user-select: none;
  touch-action: manipulation;
  outline: 10px solid rgb(var(--primary), .5);
  border-radius: 100%;
  position: relative;
  transition: .3s;
}

.btn-class-name .back {
  background: rgb(var(--secondary));
  border-radius: 100%;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.btn-class-name .front {
  background: linear-gradient(0deg, rgba(var(--primary), .6) 20%, rgba(var(--primary)) 50%);
  box-shadow: 0 .5em 1em -0.2em rgba(var(--secondary), .5);
  border-radius: 100%;
  position: absolute;
  border: 1px solid rgb(var(--secondary));
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  font-weight: 600;
  font-family: inherit;
  transform: translateY(-15%);
  transition: .15s;
  color: rgb(var(--secondary));
}

.btn-class-name:active .front {
  transform: translateY(0%);
  box-shadow: 0 0;
}

.skills-div{
    background-image: url('../Assets/skills_bg.jpg'); 
    height: 100%;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 0;
}

:root{
	--dark-blue: #1c2739;
	--darker-blue: #141c29;
	--light-blue: #3c4759;
	--text-color: #dadada;
	--dark-blue-shadow: #111822ab;
}

#skills-container{
	mask: linear-gradient(90deg, transparent, white 40%, white 60%, transparent);
	-webkit-mask: linear-gradient(90deg, transparent, white 40%, white 60%, transparent);
}

ul{
	display: flex;
	gap: 25px;
	list-style: none;
	padding: 0;
}

li{
	background: var(--light-blue);
	color: var(--text-color);
  font-size: 20px ;
	padding: 20px 40px;
	border-radius: 10px;
}

#skills-container[data-animated]{
	overflow: hidden;
}
#skills-container[data-animated] #skills-list{
	width: max-content;
	animation: scroll 40s linear infinite;
}

@keyframes scroll{
	to{
		transform: translateX(calc(-50% - 5px));
	}
}

.cloud-btn {
  position: relative;
  padding: 12px 35px;
  border-radius: 25px;
  border: none;
  margin-top: 30px;
  margin-bottom: 20px;
  background-color:  #f2f2ed;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 7px 5px 6px 5px rgba(43, 43, 43, 0.84);
}

.cloud-text {
  font-size: 20px;
  position: relative;
  z-index: 1;
}

.cloud-left {
  width: 40px;
  height: 40px;
  position: absolute;
  background-color:  #f2f2ed;
  transform: translateX(-40%) translateY(-50%);
  border-radius: 20px;
}

.cloud-right {
  width: 50px;
  height: 50px;
  position: absolute;
  background-color:  #f2f2ed;
  border-radius: 30px;
  transform: translateX(40%) translateY(-50%);
}

.carousel {
  position: relative;
  z-index: 1;
  height: 100vh;
  overflow: hidden;
  pointer-events: none;
  /* background: transparent; */
}

.carousel-item {
  --items: 7;
  --width: clamp(150px, 30vw,  300px);
  --height: clamp(200px, 40vw,  400px);
  --x: calc(var(--active) * 800%);
  --y: calc(var(--active) * 200%);
  --rot: calc(var(--active) * 120deg);
  --opacity: calc(var(--zIndex) / var(--items) * 3 - 2);
  overflow: hidden;
  position: absolute;
  z-index: var(--zIndex);
  width: var(--width);
  height: var(--height);
  margin: calc(var(--height) * -0.5) 0 0 calc(var(--width) * -0.5);
  border-radius: 10px;
  top: 50%;
  left: 50%;
  user-select: none;
  transform-origin: 0% 100%;
  box-shadow: 0 10px 50px 10px rgba(0, 0, 0, .5);
  background: transparent;
  pointer-events: all;
  transform:  translate(var(--x), var(--y)) rotate(var(--rot)) ;
  transition: transform .8s cubic-bezier(0, 0.02, 0, 1);

  .carousel-box {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: opacity .8s cubic-bezier(0, 0.02, 0, 1);
    opacity: var(--opacity);
    font-family: 'Orelo-sw-db', serif;
    background: black;

    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to bottom, rgba(0, 0, 0, .3), rgba(0, 0, 0, 0) 30%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, .5));
    }
  }

  .title {
    position: absolute;
    z-index: 1;
    color: #fff;
    bottom: 20px;
    left: 20px;
    transition: opacity .8s cubic-bezier(0, 0.02, 0, 1);
    font-size: clamp(20px, 3vw, 30px);
    text-shadow: 0 4px 4px rgba(0, 0, 0, .1);
  }

  .num {
    position: absolute;
    z-index: 1;
    color: #fff;
    top: 10px;
    left: 20px;
    transition: opacity .8s cubic-bezier(0, 0.02, 0, 1);
    font-size: clamp(20px, 10vw, 80px);
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
  }
}

.layout {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;

  &:before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 90px;
    width: 10px;
    height: 100%;
    border: 1px solid #fff;
    border-top: none;
    border-bottom: none;
    opacity: .15;
  }

  .box {
    position: absolute;
    bottom: 0;
    left: 30px;
    color: #fff;
    transform-origin: 0% 10%;
    transform: rotate(-90deg);
    font-size: 9px;
    line-height: 1.4;
    text-transform: uppercase;
    opacity: .4;
  }
}


.cursor {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  --size: 40px;
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, .2);
  margin: calc(var(--size) * -0.5) 0 0 calc(var(--size) * -0.5);
  transition: transform .85s cubic-bezier(0, 0.02, 0, 1);
  display: none;
  pointer-events: none;
  
  @media (pointer: fine) {
    display: block;
  }
}

.cursor2 {
  --size: 2px;
  transition-duration: .7s;
}
